.sign-in-form-labels {
    color: white;
}

.sign-in-form-inputs {
    background: linear-gradient(200deg, rgb(4, 4, 214), black, rgb(4, 4, 214));
    padding: 5px;
    width: 60%;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 10px;
    color: #fff;
    box-sizing: border-box;
}

.sign-in-form-textarea {
    background: linear-gradient(200deg, rgb(4, 4, 214), black, rgb(4, 4, 214));
    padding: 5px;
    width: 60%;
    display: inline-block;
    color: #fff;
    border: 1px solid #ccc;
    border-radius: 10px;
    box-sizing: border-box;
}

.card-container7 {
    height: 560px;
    width: 400px;
    overflow: hidden;
    box-shadow: 0px 0px 15px -5px;
    background: black;
}

.underline7 {
    height: 04px;
    width: 8rem;
    background-color: #fff;
    margin-top: 10px;
    margin-bottom: 10px;
  }

.card-container7:hover {
    height: 560px;
    width: 400px;
    overflow: hidden;
    box-shadow: 0px 0px 15px -5px;
    background: black;
}

.image-container7 img{
    overflow: hidden;
    height: 200px;
    width: 400px;
}

.image-container7:hover img{
    overflow: hidden;
    height: 200px;
    width: 400px;
}

.card-content {
    margin: 1rem;
    margin-top: 0.5rem;
}

h3,p {
    margin:0;
    padding: 0;
}

.card-title {
    margin-bottom: 0.5rem;
}

.card-btn {
    display: flex;
    justify-content: center;
    margin: 10px;
}