.contact {
    margin-top: 550px;
    display: flex;
    justify-Content: center;
    align-Items: center;
    background: grey;
}

.contact-form-labels {
color: white;
}

.contact-form-inputs {
background: linear-gradient(200deg, rgb(4, 4, 214), black, rgb(4, 4, 214));
padding: 10px;
color: #fff;
width: 65%;
display: inline-block;
border: 1px solid #ccc;
border-radius: 10px;
box-sizing: border-box;
}

.contact-form-textarea {
background: linear-gradient(200deg, rgb(4, 4, 214), black, rgb(4, 4, 214));
padding: 5px;
width: 60%;
display: inline-block;
border: 1px solid #ccc;
border-radius: 10px;
box-sizing: border-box;
}

.card-container4 {
align-items: center;
justify-content: center;
height: 580px;
width: 400px;
overflow: hidden;
box-shadow: 0px 0px 15px -5px;
background: black;
}

.underline4 {
height: 04px;
width: 18rem;
background-color: #fff;
margin-top: 0px;
margin-bottom: 10px;
}

.card-container4:hover {
height: 580px;
width: 400px;
overflow: hidden;
box-shadow: 0px 0px 15px -5px;
background: black;
}

.image-container4 img{
overflow: hidden;
height: 200px;
width: 400px;
}

.image-container4:hover img{
overflow: hidden;
height: 200px;
width: 400px;
}

.contact-inputs {
background: linear-gradient(200deg, rgb(4, 4, 214), black, rgb(4, 4, 214));
  padding: 5px;
  width: 100%;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 10px;
  box-sizing: border-box;
}

.contact-label {
margin-top: -30px;
margin-left: 10px;
    display: flex;
    justify-Content: center;
    align-Items: center;
color: black;
}